<template>
    <div class="login">
        <div class="container sm:px-10">
            <div class="block xl:grid grid-cols-2 gap-4">
                <!-- BEGIN: Login Info -->
                <div class="hidden xl:flex flex-col min-h-screen">
                    <a href="" class="-intro-x flex items-center pt-5">
                        <img alt="Midone - HTML Admin Template" class="w-6"
                            src="../../assets/admin-assets/dist/images/logo.svg">
                        <span class="text-white text-lg ml-3"> Admin </span>
                    </a>
                    <div class="my-auto">
                        <img alt="Midone - HTML Admin Template" class="-intro-x w-1/2 -mt-16"
                            src="../../assets/admin-assets/dist/images/illustration.svg">
                        <div class="-intro-x text-white font-medium text-4xl leading-tight mt-10">
                            A few more clicks to
                            <br>
                            sign in to your account.
                        </div>
                        <div class="-intro-x mt-5 text-lg text-white text-opacity-70 dark:text-slate-400">Manage all your
                            e-commerce accounts in one place</div>
                    </div>
                </div>
                <!-- END: Login Info -->
                <!-- BEGIN: Login Form -->
                <div class="h-screen xl:h-auto flex py-5 xl:py-0 my-10 xl:my-0">
                    <div
                        class="my-auto mx-auto xl:ml-20 bg-white dark:bg-darkmode-600 xl:bg-transparent px-5 sm:px-8 py-8 xl:p-0 rounded-md shadow-md xl:shadow-none w-full sm:w-3/4 lg:w-2/4 xl:w-auto">
                        <h2 class="intro-x font-bold text-2xl xl:text-3xl text-center xl:text-left">
                            Sign In
                        </h2>
                        <div class="intro-x mt-2 text-slate-400 xl:hidden text-center">A few more clicks to sign in to your
                            account. Manage all your e-commerce accounts in one place</div>
                        <form v-on:submit.prevent="loginAdmin" method="post">
                            <!-- v-on:submit.prevent="loginAdmin"  -->

                            <div class="intro-x mt-8">
                                <input type="text" class="intro-x login__input form-control py-3 px-4 block"
                                    placeholder="Email" v-model="posts.email">
                                <p v-if="formErrors.email" class="text-danger">{{ formErrors.email }}</p>
                                <span v-if="emailError" class="text-danger">{{ emailError }}</span>
                                <input type="password" class="intro-x login__input form-control py-3 px-4 block mt-4"
                                    placeholder="Password" v-model="posts.password">
                                <p v-if="formErrors.password" class="text-danger">{{ formErrors.password }}</p>
                                <span v-if="pwdError" class="text-danger">{{ pwdError }}</span>

                            </div>
                            <div class="intro-x flex text-slate-600 dark:text-slate-500 text-xs sm:text-sm mt-4">
                                <router-link to="/admin/forgot/password">Forgot Password?</router-link>
                            </div>
                            <div class="intro-x mt-5 xl:mt-8 text-center xl:text-left">

                                <button class="btn btn-primary py-3 px-4 w-full xl:w-32 xl:mr-3 align-top"
                                    type="submit">Login</button>
                                <span v-if="tokenError">{{ tokenError }}</span>
                              </div>
                        </form>
                    </div>


                </div>
                <!-- END: Login Form -->
            </div>
        </div>
    </div>
    <!-- /.login-box -->
</template>

<script>
import { loginRequest } from '../../pages/ApiFunctions';
import * as Yup from 'yup';

const schema = Yup.object().shape({
    email: Yup.string().email().required("Email is required"),
    password: Yup.string().required("Password is required"),
});
export default {
    name: "Adminlogin",
    data() {
        return {
            emailError: '',
            pwdError: '',
            tokenError: '',
            posts: {
                email: '',
                password: ''
            },
            formErrors: {},
        }
    },
    methods: {
        async loginAdmin() {
            try {
                await schema.validate(this.posts, { abortEarly: false });
                const res = await loginRequest('/admin/login', this.posts)
                if (res.error) {
                    this.emailError = res.error;
                }
                else if (res.passwordError) {
                    this.pwdError = res.passwordError;
                }
                else {
                    this.emailError = '',
                        this.pwdError = ''
               
                    if (res && res.data && res.data.token) {
                        localStorage.setItem('token', res.data.token || '')
                    }
                     else {
                        this.tokenError = 'Invalid Token'
                    }
                    this.$router.push({
                        path: '',
                        name: 'Dashboard'
                    })
                }
            }
            catch (error) {
                if (error.name === 'ValidationError') {
                    const formErrors = {}
                    error.inner.forEach((e) => {
                        formErrors[e.path] = e.message
                    })
                    this.formErrors = formErrors
                }
            }
        }

    }
}

</script>
<style>
@import '../../assets/admin-assets/dist/css/app.css';
</style>



